import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { CustomerDebtComponent } from './customer-debt.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';

const routes: Routes = [
  {
    path: '',
    component: CustomerDebtComponent
  },
  {
    path: 'detail-customer-debt',
    loadChildren: () => import('./detail-customer-debt/detail-customer-debt.module').then(m => m.DetailCustomerDebtModule)
  }
]

@NgModule({
  declarations: [CustomerDebtComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    IonicModule,
  ]
})
export class CustomerDebtModule { }
